:root {
    --card-height: 300px;
    --card-width: calc(var(--card-height) / 1.5);
}
* {
    box-sizing: border-box;
}
body {
    /* width: 100vw; */
    height: 100vh;
    margin: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #191c29;
}
.card-3d {
    /* width: var(--card-width); */
    height: var(--card-height);
    position: relative;
    display: flex;
    justify-content: center;
    align-items: flex-end;
    padding: 0 36px;
    perspective: 2500px;
    /* margin: 0 50px; */
}

.cover-image {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.wrapper {
    transition: all 0.5s;
    position: absolute;
    /* width: 100%; */
    /* height: 100%; */
    z-index: -1;
}

.card-3d:hover .wrapper {
    transform: perspective(900px) translateY(15%) rotateX(65deg) translateZ(0);
    box-shadow: 2px 35px 32px -8px #111111c4;
    -webkit-box-shadow: 2px 35px 32px -8px #111111c4;
    -moz-box-shadow: 2px 35px 32px -8px #111111c4;
    /* background-color: rgba(12, 13, 19, 0.5) 68%, */
    opacity : 0.2;
}

.wrapper::before,
.wrapper::after {
    content: "";
    opacity: 0;
    width: 100%;
    height: 80px;
    transition: all 0.5s;
    position: absolute;
    left: 0;
}
.wrapper::before {
    top: 0;
    height: 100%;
    background-image: linear-gradient(
        to top,
        transparent 5%,
        #111111ac 48%,
        #111 77%
    );
}
.wrapper::after {
    bottom: 0;
    opacity: 1;
    background-image: linear-gradient(
        to bottom,
        transparent 15%,
        #111111ac 68%,
        #111111 97%
    );
}

.card-3d:hover .wrapper::before,
.wrapper::after {
    opacity: 1;
}

.card-3d:hover .wrapper::after {
    height: 120px;
}
.title {
    width: 100%;
    transition: transform 0.5s;
}
.card-3d:hover .title {
    transform: translate3d(0%, -50px, 100px);
}

.character {
    /* width: 100%; */
    opacity: 0;
    transition: all 1.5s;
    position: absolute;
    z-index: -1;
}

.card-3d:hover .character {
    opacity: 1;
    transform: translate3d(0%, -5%, 100px);
}
